.jumbo {
    background-image: url('../images/road.jpg');
    background-size: cover;
    height: 100vh;
    background-repeat: no-repeat;
    overflow-y: hidden;
}

.container-fluid, .container-lg, .container-md, .container-sm, .container-xl {
    width: 100%;
    padding-right: 0px;
    padding-left: 0px;
    margin-right: auto;
    margin-left: auto;
}

.jumbotron {
    padding: 2rem 1rem;
    margin-bottom: 2rem;
    background-color: #e9ecef;
    border-radius: 0rem;
}

.content {
    position: absolute; 
    left: 0; 
    right: 0;
    margin-top: 200px; 
    margin-left: auto; 
    margin-right: auto; 
    width: 150px;
    height: 150px;
    border-radius: 80px;
    font-size: 20px;
    background-color:#df993f;
    border-color: white;
    border-width: 2px;
  }

